<template>
  <div class="">
    <div class="shopcar clearfix pr h100">
      <div class="header header-scroll">
        <div class="content">
          <img src="../../../assets/image/icon/shopcar-productlist.png" height="29" width="38" alt="">
          <span>商品列表</span>
        </div>
      </div>
      <div class="shopcarleft w50 pull-left pl-25" style="position: absolute;overflow: scroll;height:118%;top: 1.5rem;">

        <ul class="productlist detailslist-scroll">
          <li v-if="ringdata.length != 0" v-for="item in ringdata" class="product-item clearfix">
            <div class="img">
              <img :src="item.info.pic" height="150" width="150" alt="">
            </div>
            <div class="details">
              <ul class="detailslist bb1 clearfix ring">
                <li class="pull-left f000 pr">
                  名称<span class="f000 name pa">{{item.info.name}}</span>
                </li>
                <li class="pull-left f000">
                  款号<span class="f000">{{item.info.fnum}}</span>
                </li>
                <li class="pull-left f000">
                  材质<span>{{item.info.material}}</span>
                </li>
                <li class="pull-left">
                  手寸<span>{{item.info.handsize}}</span>
                </li>
              </ul>
              <div class="bb1 pr">
                <p>¥ <span class="fc-blue">{{parseFloat(item.info.price).toFixed(2)}}</span> 元</p>
              </div>
            </div>
          </li>
          <li v-if="diamondsdata.length != 0" v-for="item in diamondsdata" class="product-item clearfix">
            <div class="img">
              <img v-if="item.info.shape == 'ROUND' " src="../../../assets/image/round.png" height="393" width="393"
                   alt=""><!--圆形-->
              <img v-if="item.info.shape == 'PRINCESS' " src="../../../assets/image/cushion.png" height="393"
                   width="393" alt=""><!--公主房-->
              <img v-if="item.info.shape == 'EMERALD' " src="../../../assets/image/zuanshi_emerald.png" height="393"
                   width="393" alt=""><!--祖母绿-->
              <img v-if="item.info.shape == 'MARQUISE' " src="../../../assets/image/marquise.png" height="393"
                   width="393" alt=""><!--橄榄型-->
              <img v-if="item.info.shape == 'OVAL' " src="../../../assets/image/oval.png" height="393" width="393"
                   alt=""><!--椭圆形-->
              <img v-if="item.info.shape == 'ASSCHER' " src="../../../assets/image/Asscher.png" height="393"
                   width="393" alt=""><!--雷蒂恩-->
              <img v-if="item.info.shape == 'PEAR' " src="../../../assets/image/pear.png" height="393" width="393"
                   alt=""><!--水滴形-->
              <img v-if="item.info.shape == 'HEART' " src="../../../assets/image/heart.png" height="393" width="393"
                   alt=""> <!--心形-->
              <img v-if="item.info.shape == 'TRILLION' " src="../../../assets/image/round_2_3.jpg" height="393"
                   width="393" alt=""><!--三角形-->
              <img v-if="item.info.shape == 'CUSHION' " src="../../../assets/image/radiant.png" height="393"
                   width="393" alt=""><!--垫形-->
            </div>
            <div class="details">
              <ul class="detailslist  bb1 clearfix ring">
                <li class="pull-left f000 pr">
                  货号<span class="f000 name pa">{{item.info.ref}}</span>
                </li>
                <li class="pull-left f000">
                  <span style="margin-left: -.26rem;" v-if="item.info.shape=='ROUND'">圆形</span>
                  <span style="margin-left: -.26rem;" v-if="item.info.shape=='PRINCESS'">公主方</span>
                  <span style="margin-left: -.26rem;" v-if="item.info.shape=='EMERALD'">祖母绿</span>
                  <span style="margin-left: -.26rem;" v-if="item.info.shape=='HEART'">心形</span>
                  <span style="margin-left: -.26rem;" v-if="item.info.shape=='OVAL'">椭圆形</span>
                  <span style="margin-left: -.26rem;" v-if="item.info.shape=='PEAR'">水滴形</span>
                  <span style="margin-left: -.26rem;" v-if="item.info.shape=='MARQUISE'">橄榄形</span>
                  <span style="margin-left: -.26rem;" v-if="item.info.shape=='ASSCHER'">雷蒂恩</span>
                  <span style="margin-left: -.26rem;" v-if="item.info.shape=='CUSHION'">垫形</span>
                  <span style="margin-left: -.26rem;" v-if="item.info.shape=='TRILLION'">三角形</span>
                  <span style="margin-left: -.26rem;">{{item.info.size}}</span>
                </li>
                <li class="pull-left">
                  颜色<span>{{item.info.color}}</span>
                </li>
                <li class="pull-left">
                  净度<span>{{item.info.clarity}}</span>
                </li>
                <li class="pull-left">
                  切工<span>{{item.info.cut}}</span>
                </li>
                <li class="pull-left">
                  抛光<span>{{item.info.polish}}</span>
                </li>
                <li class="pull-left">
                  对称<span>{{item.info.sym}}</span>
                </li>
              </ul>
              <div class="bb1 pr">
                <p>¥ <span class="fc-blue">{{parseFloat(item.info.price).toFixed(2) }}</span> 元</p>
              </div>
            </div>
          </li>
        </ul>
      </div>
      <div class="shopcarright w50 pull-left pl1 pr-25" style="margin-left: 50%;">
        <div class="header ">
          <div class="content">
            <img src="../../../assets/image/icon/shopcar-productdetails.png" height="27" width="34" alt="">
            <span>订单信息</span>
          </div>
        </div>
        <div class="shopcarrightcontent">
          <ul>
            <li class="clearfix">
              <span class="title ">顾客姓名 </span>
              <span class="w80 fblack" type="text">{{orderdetail.cname}}</span>
            </li>
            <li class="clearfix">
              <span class="title">顾客电话 </span>
              <span class="w80 fblack" type="text" v-html="orderdetail.cphone"></span>
            </li>
            <li class="clearfix">
              <span class="title">顾客备注 </span>
              <span style="padding-left: .1rem" class="textarea w80 dis-b textarea-shop">{{orderdetail.cremark}}</span>
            </li>
            <li class="clearfix">
              <span class="title">销售人员 </span>
              <span class="w80 fblack" type="text" v-text="orderdetail.salename"></span>
            </li>
            <li class="clearfix line pr">
              <span class="title"></span>
              <div class="w80"></div>
            </li>
            <li class="clearfix" v-if="orderdetail.cash != '0' ">
              <span class="title">优惠价格 </span>
              <span class="w30 fcBlue" type="text">¥ {{parseFloat(orderdetail.cash).toFixed(2)}}</span><span v-if="(/^[1-9]$/.test(discount))" class=" tips"> {{discount}} 折</span>
            </li>
            <li class="clearfix">
              <span class="title">预收定金 </span>
              <span class="w30 fcBlue" type="text">¥ {{parseFloat(orderdetail.deposit).toFixed(2)}}</span><span v-if="(/^[1-9]\d*$/.test(earnestratio))" class=" tips">{{earnestratio}} %</span>
            </li>
            <li class="clearfix">
              <span class="title">尾款需付 </span>
              <span class="w30 fcBlue" type="text">¥ {{parseFloat(orderdetail.tailcash).toFixed(2)}}</span><span v-if="(/^[1-9]\d*$/.test(finalratio))" class=" tips">{{finalratio}} %</span>
            </li>
            <li class="clearfix line pr">
              <span class="title"></span>
              <div class="w80"></div>
            </li>
            <li class="clearfix">
              <span class="title fc-red">订单状态 </span>
              <!-- 1、待审核（IPAD端显示待处理）
              2、待建模（深度定制特有）
              3、建模中（开始建模后，等待输入定价）
              4、待顾客确认（深度定制特有）
              5、待定制（审核通过的订单）
              6、定制中（开始定制后，等待发货）
              7、待确认收货（已经发货，等待门店确认收货）
              8、已完成（门店已经确认收货，订单结束了）
              9、审核不通过（订单审核没有通过）
              10、已关闭（订单由后台主动关闭）-->
              <!--"-1审核失败 0待审核 1未定义 2审核通过 3制作中 4制作但暂停中(变更) 5已发货 6已到货 9已关闭10建模中11待定价12待顾客确认13顾客已确认"-->
              <span v-if="orderdetail.status == '0'"  class="w80 fc-red" type="text" >待审核</span>
              <span v-if="orderdetail.status == '1'"  class="w80 fc-red" type="text" >未定义</span>
              <span v-if="orderdetail.status == '2' && orderdetail.ordertype == '3'"  class="w80 fc-red" >待建模</span>
              <span v-if="orderdetail.status == '2' && orderdetail.ordertype != '3'"  class="w80 fc-red" >待定制</span>
              <span v-if="orderdetail.status == '3'"  class="w80 fc-red" type="text" >定制中</span>
              <span v-if="orderdetail.status == '4'"  class="w80 fc-red" type="text" >定制中</span>
              <span v-if="orderdetail.status == '5'"  class="w80 fc-red" type="text" >待确认收货</span>
              <span v-if="orderdetail.status == '6'"  class="w80 fc-red" type="text" >已完成</span>
              <span v-if="orderdetail.status == '9'"  class="w80 fc-red" type="text" >已关闭</span>
              <span v-if="orderdetail.status == '10'"  class="w80 fc-red" type="text" >建模中</span>
              <span v-if="orderdetail.status == '11'"  class="w80 fc-red" type="text" >建模中</span>
              <span v-if="orderdetail.status == '12'"  class="w80 fc-red" type="text" >待顾客确认</span>
              <span v-if="orderdetail.status == '13'"  class="w80 fc-red" type="text" >待定制</span>
              <span v-if="orderdetail.status == '-1'"  class="w80 fc-red" type="text" >审核不通过</span>
            </li>
            <li v-if="orderdetail.status == '5'" class="clearfix">
              <span class="title">快递公司 </span>
              <span disabled="disabled" class="w80" type="text" v-html="orderdetail.logistic"></span>
            </li>
            <li v-if="orderdetail.status == '5'" class="clearfix">
              <span class="title">快递单号 </span>
              <span disabled="disabled" class="w80" type="text" v-html="orderdetail.logistic_num"></span>
            </li>
            <li class="clearfix line pr">
              <span class="title"></span>
              <div class="w80"></div>
            </li>
            <li class="clearfix">
              <span class="title"></span>
              <button v-if="orderdetail.status == '5'" class="mybtn w80 pull-left"
                      @click="ConfirmReceipt(id)"> 确认收货
              </button>
              <router-link v-else :to="{name:'OrderlistHome',query:{page:page}}"
                           class="mybtn w80 pull-left"> 返回
              </router-link>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
  export default {
    created() {
      this.initialize()
      this.routefun()
    },
    data() {
      return {
        /*传过来的id*/
        id: this.$route.query.id,
        /*订单数据*/
        orderdetail: '',
        /*戒托信息*/
        ringdata: [],
        /*裸钻信息*/
        diamondsdata: [],
        /*确认收货按钮显示状态*/
        btnisshow: '',
        /*折扣*/
        discount: '',
        /*定金比例*/
        earnestratio: '',
        /*尾款比例*/
        finalratio: '',
        /*传递过来的页码*/
        page:0,
      }
    },
    methods: {
      /*初始化参数*/
      initialize() {
        var url = this.BASEURL='/index.php?action=order_detail';
        this.$http.get(url, {
          params: {
            orderid: this.id,
          }
        }, {}).then(response => {
          if (response.body.error_code == 0) {
            this.orderdetail = response.body.orderinfo;
            for (var k in this.orderdetail.orderinfo) {
              if (this.orderdetail.orderinfo[k]['type'] == '1') {
                this.ringdata.push(this.orderdetail.orderinfo[k])
              } else if (this.orderdetail.orderinfo[k]['type'] == '2') {
                this.diamondsdata.push(this.orderdetail.orderinfo[k])
              }
            }
            /*折扣*/
            this.discount = this.zhekou(this.orderdetail.price, this.orderdetail.cash)
            /*计算定金和尾款比例前，先判断是否有优惠价格*/
            if (this.orderdetail.cash == '0' || this.orderdetail.cash == '') {
              /*定金比例*/
              this.earnestratio = this.priceratio(this.orderdetail.price, this.orderdetail.deposit)
              /*尾款比例*/
              this.finalratio = this.priceratio(this.orderdetail.price, this.orderdetail.tailcash)
            } else {
              /*定金比例*/
              this.earnestratio = this.priceratio(this.orderdetail.cash, this.orderdetail.deposit)
              /*尾款比例*/
              this.finalratio = this.priceratio(this.orderdetail.cash, this.orderdetail.tailcash)
            }

          } else if (response.body.error_code == '90009' || response.body.error_code == '90005') {
            parent.location.href = '/login';
            return false
          }
        }, response => {
          console.log(response);
        });
      },
      /*路由传参方法*/
      routefun(){
        if(this.$route.query.id){
          this.id = this.$route.query.id
        }
        if(this.$route.query.page){
          this.page = this.$route.query.page
        }
      }

    },
    watch: {
      $route(){
        this.initialize()
        this.routefun()
      }
    }
  }
</script>
<style>


</style>



