<template>
  <div class="login-box clearfix h100" id="loginbox">
    <div class="clearfix box">
      <div class="left">
        <img src="../../assets/image/icon/logo_03.png" alt="KISSRING" class="logo">
      </div>
      <div class="">
        <form action="javascript:;" @submit="submitdata()">
          <div class="form-group bb1 before">
            <input type="tel" class="form-control form-control-img" v-model="tel" name="tel" id="tel"
                   placeholder="手机号码">
          </div>
          <div class="form-group bb1 before">
            <input type="password" class="form-control form-control-img " v-model="password" name="password"
                   id="password" placeholder="登录密码">
          </div>
          <!--<div class="login_lostpassword"><a class="orange" href="/login_lostpassword">忘记密码</a></div>-->
          <input type="submit" class="mybtn" value="登 录">
        </form>
      </div>
    </div>
    <!-- 公共组件 -->
    <!-- 提示窗 -->
    <!-- 1、没有输入手机号码反馈提示：
    2、手机号码格式错误提示：
    3、系统不存的的帐号或者密码错误提示：
    4、登陆成功提示：
    请输入登录手机号码
    手机号码不正确
    帐号或登陆密码不正确 -->
  </div>
</template>
<script type="text/ecmascript-6">
  import crypto from 'crypto'
  export default {
    data() {
      return {
        tel: '',
        password: '',
        messagedata: ''
      }
    },
    methods: {
      submitdata() {
        /*md5加密*/
        var a;
        var md5 = crypto.createHash("md5");
        md5.update(this.password);
        a = md5.digest('hex');

        if (this.tel == "") {
          this.messagedata = '请输入登陆手机号码';
          this.message();
          return false
        } else if (!(/^1(3|4|5|7|8)\d{9}$/.test(this.tel))) {
          this.messagedata = '手机号码不正确';
          this.message();
          return false
        } else if (this.password == "") {
          this.messagedata = '请输入登陆密码';
          this.message();
          return false
        } else {
          var url = this.BASEURL+'/index.php?action=login';
          this.$http.post(url, {
            mobile: this.tel,
            password: a
          }, {emulateJSON: true}).then(response => {
            if (response.body.error_code == '0') {
              this.$router.push({path: '/index'});
            } else if (response.body.error_code == '90002') {
              this.messagedata = '登录密码错误';
              this.message();
            } else if (response.body.error_code == '90003') {
              this.messagedata = '登录账号不存在';
              this.message();
            }
          }, response => {
            console.log(response);
          });
        }
      },
      /*查询数据整理*/
      clearsearchlist(demo) {
        var str = "";
        demo.forEach(
          function (item) {
            str += item + "-"
          }
        )
        return str;
      },
    }
  }

</script>
<style>
  .login_lostpassword {
    text-align: right;
    padding-top: .2rem;
    padding-bottom: .4rem;
  }
</style>



