<template>
  <div class="depthcustomhome">
    <div class="shopcar clearfix pr">
      <div class="header header-scroll " style="width: 40%;right: auto">
        <div class="content">
          <img src="../../../assets/image/icon/shopcar-productlist.png" height="29" width="38" alt="">
          <span>商品信息</span>
          <div style="display: initial;">
            <button id='addPic' href="" @click="addPic" class="pull-right btn-w sm-btn-w">添加图片</button>
            <input type="file" @change="onFileChange" multiple style="display: none;">
          </div>
        </div>
      </div>
      <div class="shopcarleft w40 pull-left  pr" style="position: absolute;overflow: scroll;height: 120%;top: 1.5rem">
        <div class="">
          <div class="shopcarleftcontent detailslist-scroll">
            <div v-if="images.length >0">
              <ul class="imgul">
                <li class="imgli" v-for="(image,key) in images">
                  <div class="imgimg" :style="{backgroundImage: 'url(' + image + ')'}" @click='delImage(key)'></div>
                  <a class="imgdelete" href="#" style="position: absolute;" @click='delImage(key)'>
                  </a>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
      <div class="shopcarright w60 pull-left pr-25" style="margin-left: 40%;padding-left: .8rem">
        <div class="header">
          <div class="content">
            <img src="../../../assets/image/icon/shopcar-productdetails.png" height="27" width="34" alt="">
            <span>订单信息</span>
            <button type="text" @click="adddata()" class="pull-right btn-w sm-btn-w">添加参数</button>
          </div>
        </div>
        <div class="shopcarrightcontent ">
          <form action="javascript:;" @submit="senddata()">
            <table class="table table-striped table-hover import-hover mb1 ">
              <thead>
              <tr>
                <th class="w10">序号</th>
                <th>类别</th>
                <th>项目</th>
                <th>数量</th>
                <th>单价</th>
                <th class="subtotalbox">小计</th>
                <th class="w10">删除</th>
              </tr>
              </thead>
              <tbody>
              <tr v-for="(item,index) in datalist">
                <td class="index">{{index + 1}}</td>
                <td>
                  <select v-model="item.texture" class="type bb1" name="">
                    <option :value="item" v-for="item in typelist">{{item}}</option>
                  </select>
                </td>
                <td>
                  <select v-model="item.weight" class="project bb1" name="">
                    <option :value="iteme.name" v-for="iteme in item.parameteritemdata">{{iteme.name}}</option>
                  </select>
                </td>
                <td>
                  <div class="box bd1">
                    <input step="0.01" v-model="item.num"  type="number" name="" value=""><span>{{item.parameteritemunit}}</span>
                  </div>
                </td>
                <td>
                  <input step="0.01" v-model="item.unitprice" disabled="disabled" class="w100 tac" type="number" name="" value="">
                </td>
                <td class="subtotalbox">
                  <input step="0.01" @focus ="tips()" @blur ="tips()"  v-model="item.totalprice" class="w100 bb1 tac" type="number" name="" value="">
                </td>
                <td>
                  <span class="delatedatabox" @click="delate(index + 1)" >
                  </span>
                </td>
              </tr>
              </tbody>
            </table>
            <div class="w100 bt1 bb1 pt-4 pb-4 other ">
              <div class="clearfix">
                <p class="pull-left">
                  <span class="fc-gray">手寸：</span>
                  <el-select clearable v-model="handsize" size="small" placeholder="请选择">
                    <el-option value="">请选择</el-option>
                    <el-option value="10">10</el-option>
                    <el-option value="11">11</el-option>
                    <el-option value="12">12</el-option>
                    <el-option value="13">13</el-option>
                    <el-option value="14">14</el-option>
                    <el-option value="15">15</el-option>
                    <el-option value="16">16</el-option>
                  </el-select>
                </p>
                <p class="pull-left"><span class="fc-gray">刻字：</span>
                  <el-input class="w60" v-model="lettering" placeholder="" style="width: 70%"></el-input>
                </p>
              </div>
              <div class="textareabox">
                <span class="fc-gray">定制说明：</span> &nbsp;
                <el-input
                  type="textarea"
                  :autosize="{ minRows: 2, maxRows: 6}"
                  placeholder="顾客特别说明"
                  v-model="cremark">
                </el-input>
              </div>
            </div>
            <div class="bottom">
              <div class="total pb-2">
                <span class="f36 ">合计：</span>
                <span class="fc-blue">约 ¥ <span class="fc-blue fw-b f36">{{price}}</span> 元</span>
              </div>
              <p class="fc-gray pb-2">该费用为预估的，费用会有10%上下的误差，最终价格以建模后价格为准。</p>
              <div class="">
                <button type="reset" class="btn-w" @click="cleardata()">重新选择</button>
                <button type="submit" class="btn-b ml-2">提交定制</button>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
  var tip = false/*0为关闭，1位开启*/
  export default {
    created(){
      this.initialize()
    },
    data() {
      return {
        /*图片控件*/
        images: [],
        cremark: '', /*定制说明*/
        lettering: '', /*刻字*/
        handsize: '', /*手寸*/
        price: 0, /*价格*/
        /*参数列表*/
        datalist: [],
        isshow: false,
        /*后台定制参数*/
        parameter: '',
        parameteritem: '',
        parameteritemdata: '',
        /*类别列表*/
        typelist:[],
      }
    },
    methods: {
      delate(val){
        this.datalist.splice(val-1,1)
      },
      /*记录是否手动改动价格（如果为true为手动，false为自动）*/
      tips(){
        if(tip){
          tip=false
        }else {
          tip=true
        }
      },
      addPic(e){
        e.preventDefault();
        $('input[type=file]').trigger('click');
        return false;
      },
      onFileChange(e) {
        var files = e.target.files || e.dataTransfer.files;
        if (!files.length)return;
        this.createImage(files);
      },
      createImage(file) {
        if (typeof FileReader === 'undefined') {
          alert('您的浏览器不支持图片上传，请升级您的浏览器');
          return false;
        }
        var vm = this;
        var leng = file.length;
        for (var i = 0; i < leng; i++) {
          var reader = new FileReader();
          reader.readAsDataURL(file[i]);
          reader.onload = function (e) {
            vm.images.push(e.target.result);
          };
        }
      },
      delImage: function (index) {
        this.$confirm('确定需要删除本张图片吗', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          confirmButtonClass: 'btn-b',
          cancelButtonClass: 'btn-w',
        }).then(() => {
          this.images.shift(index);
          this.messagedata = '删除成功';
          this.message();
        }).catch(() => {
        });
      },
      /*初始化参数*/
      initialize() {
        var url = this.BASEURL+'/index.php?action=deep_dia';
        this.$http.get(url, {
          params: {}
        }).then(response => {
          if (response.body.error_code == '90009' || response.body.error_code == '90005') {
            parent.location.href = '/login';
            return false
          } else {
            this.parameter = response.body.result;
            var i = 1
            for (let k in this.parameter) {
              this.typelist.push(k)
              i++
            }
          }

        }, response => {
          console.log(response);
        });
      },
      /*添加参数*/
      adddata(){
        var a = {
          texture: '', /*参数类别 */
          weight: '', /*参数项目 */
          num: 1, /*参数数量*/
          unitprice: '', /*单价*/
          totalprice: '', /*总价*/
          parameteritemunit: '',
//          parameteritem: '',
        }
        this.datalist.push(a);
      },
      /*提交定制*/
      senddata(){
        var filelist = []
        for (var i = 0; i < this.images.length; i++) {
          filelist.push(this.images[i])
        }
        /*判断客户是否选择图片，没有选择，就直接return false,并且提示*/
        if(filelist.length == 0){
          this.messagedata = '请选择图片';
          this.message();
          return false
        }
        /*判断客户是否增加参数，没有就直接return false,并且提示*/
        if(this.datalist.length == 0){
          this.messagedata = '请添加参数';
          this.message();
          return false
        }
        /*判断添加的参数是否全部填写完毕*/
        for (var n = 0; n < this.datalist.length; n++) {
          /*当选择为裸钻的时候，限制数量最大为1*/
          if(this.datalist[n].texture == '裸钻'){
            if(this.datalist[n].num>1){
              this.messagedata = '裸钻参数不能大于1';
              this.message();
              return false
            }
          }
          for (let k in this.datalist[n]) {
            if(k != 'parameteritemdata'){
              if(this.datalist[n][k] == ''){
                this.messagedata = '参数没有填写完整';
                this.message();
                return false
              }
            }
          }
        }
        /*判断客户是否选择手寸，没有就直接return false,并且提示*/
        if (this.handsize == ''){
          this.messagedata = '请选择手寸';
          this.message();
          return false
        }
          this.isshow = false
          var url = this.BASEURL+'/index.php?action=upimg';
          this.$http.post(url, {
            cremark: this.cremark,
            lettering: this.lettering,
            handsize: this.handsize,
            info: this.datalist,
            price: this.price,
            images: filelist,
          }, {emulateJSON: true}).then(response => {
            if (response.body.error_code == '90009' || response.body.error_code == '90005') {
              parent.location.href = '/login';
              return false
            } else {
              /*带参数跳转到下个页面*/
              this.$router.push({
                name: 'DepthcustomOrderdetails',
                query: {msg: JSON.stringify(response.body.result),typelist:this.typelist},
                params: {images: filelist}
              });
            }
          });
      },
      /*清空*/
      cleardata(){
        this.cremark = ''
        /*定制说明*/
        this.lettering = ''
        /*刻字*/
        this.handsize = ''
        /*手寸*/
        this.price = 0
        /*价格*/
        /*参数列表*/
        this.datalist = []
      },
      /*监视参数变化，修改对应变化*/
      changedata(b, n, p, q, e){
        this.datalist[n].parameteritemunit = b
        this.datalist[n].unitprice = p
        /*判断价格变化*/
        var a = 0
        for (let i = 0; i < this.datalist.length; i++) {
          if(this.datalist[i].num!=''){
            /*根据tip判断手动价格还是自动价格*/
            if(!tip){
              this.datalist[i].totalprice = (q[i].num * q[i].unitprice)
            }else {
            }
          }else {
          }
          a += parseFloat(this.datalist[i].totalprice);
        }
        this.price = a.toFixed(2)
      },
    },
    computed: {
      dataRest: function () {
        var arry = [];
        for (var i = 0; i < this.datalist.length; i++) {
          var obj = {};
          for (let k in this.datalist[i]) {
            obj[k] = this.datalist[i][k]
          }
          arry.push(obj)
        }
        return arry;
      },
    },
    watch: {
      $route(){
        this.initialize()
      },
      dataRest: {
        handler: function (nowVal, oldVal) {
          var b = ''
          var p = ''
          if (nowVal.length != oldVal.length) {
            return false
          } else {
            for (var i = 0; i < nowVal.length; i++) {
              /*当选择为裸钻的时候，限制数量最大为1*/
             if(nowVal[i].texture == '裸钻'){
               if(nowVal[i].num>1){
                 this.messagedata = '裸钻参数不能大于1';
                 this.message();
               }
             }
              if (nowVal[i] != oldVal[i]) {
                for (let f in this.parameter){
                  if (nowVal[i].texture == f) {
                    this.datalist[i].parameteritemdata = this.parameter[f]
                  }
                }
                for (let o in this.parameter) {
                  for (let h = 0; h < this.parameter[o].length; h++) {
                    for (let j in this.parameter[o][h]) {
                      /*判断单位、单价*/
                      if (this.parameter[o][h][j] == nowVal[i]['weight']) {
                        b = this.parameter[o][h]['unit']
                        p = this.parameter[o][h]['total_price']
                        this.changedata(b, i, p,nowVal,oldVal)
                      }
                    }
                  }
                }
              }
            }
          }
        },
        deep: true
      },
    }
  }
</script>
<style>
  .delatedatabox{
    height: .4rem;
    width: .4rem;
    display: inline-block;
    background: url("../../../assets/image/icon/delate.png") no-repeat;
    background-size: cover;
  }
  .delatedatabox:active{
    background: url("../../../assets/image/icon/delate-active.png") no-repeat;
    background-size: cover;
  }
  ul.imgul {
    list-style: none outside none;
    margin: 0;
    padding: 0;
  }

  li.imgli {
    height: 3rem;
    width: 50%;
    display: inline-block;
    list-style: none;
    position: relative;
    padding: .1rem 0;
  }

  li.imgli:nth-child(odd) {
    box-sizing: border-box;
    padding-right: .1rem;
  }

  li.imgli:nth-child(even) {
    box-sizing: border-box;
    padding-left: .1rem;
  }

  .imgimg {
    width: 100%;
    height: 100%;
    margin: auto;
    margin-bottom: 10px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }

  .imgdelete {
    top: 15px;
    right: 15px;
  }

  .el-upload__input {
    display: none !important;
  }

  .subtotalbox {
    padding-left: 0 !important;
    padding-right: 0 !important;
  }
</style>



