<template>
  <div class="">
    <div class="conttent clearfix setting pa">
      <div class="w50 pull-left">
        <router-link :to="{name:'Changepassword'}">
          <div class="box pull-right pr settingleft">
            <div class="box-content pa">
              <img src="../../../src/assets/image/icon/chagepassword.png" height="50" width="42" alt="">
              <p class="f36 fc-white">修改密码</p>
            </div>
          </div>
        </router-link>
      </div>
      <div class="w50 pull-left">
        <router-link to="">
          <div @click="logout()" class="box pull-left pr settingright">
            <div class="sm-box clearfix h100">
              <div class="box-content pa">
                <img src="../../../src/assets/image/icon/logout.png" height="50" width="42" alt="">
                <p class="f36 fc-blue">注销</p>
              </div>
            </div>
          </div>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    methods: {
      logout() {
          var url = this.BASEURL+'/logout.php';
          this.$http.get(url).then(response => {
            if(response.body.error_code == '90009' || response.body.error_code == '90005'){
              parent.location.href = '/login';
              return false
            }else {
              this.$router.push({path: '/login'});
            }
          }, response => {
            console.log(response);
          });
        }
      }
    }
</script>

<style scoped>
  .settingright:active {
    border-color: #97f1fd;
  }

  .settingleft:active {
    background-color: #46d8e8;
  }
</style>



